body {
  background-color: #fff;
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
}

.people {
  grid-template-columns: repeat(2, 1fr);
  justify-items: center;
  gap: 30px;
  max-width: 1000px;
  margin: 0 auto;
  padding: 20px;
  display: grid;
  position: relative;
}

@media (width >= 1000px) {
  .people {
    grid-template-columns: repeat(4, 1fr);
    gap: 30px;
  }
}

person {
  text-align: center;
  background-color: #fff;
  border-radius: 10px;
  flex-direction: column;
  align-items: center;
  width: 200px;
  height: 231px;
  padding: 10px;
  display: flex;
  position: relative;
}

.person .photo {
  border-radius: 50%;
  width: 200px;
  height: 200px;
  padding: 10px;
  overflow: hidden;
}

.person img {
  object-fit: cover;
  border-radius: 50%;
  width: 100%;
  height: 100%;
}

.person .details {
  margin-top: 10px;
}

.person .name {
  color: #2b2b2b;
  margin-bottom: 5px;
  font-size: 18px;
  font-weight: bold;
  text-decoration: none;
  display: block;
}

.extra {
  border-radius: 5px;
  margin-top: auto;
  padding: 10px;
  font-size: 14px;
  display: none;
  bottom: 10px;
}

.person:hover {
  text-align: center;
  z-index: 1;
  background-color: #eee;
  border-radius: 10px;
  flex-direction: column;
  align-items: center;
  width: 220px;
  height: 287px;
  display: flex;
}

.person:hover .photo {
  width: 240px;
  height: 240px;
}

.person:hover .extra {
  display: block;
}
